  calculateZScore(model, geneSheet) {
    let pathwayInfo = [];

    for (let metaboliteName of metaboliteNames) {
        let geneInfoDict = {};

        for (let reaction of model.reactions) {

            let reactantMetabolites = reaction.reactants.map(metabolite => metabolite.id);
            let productMetabolites = reaction.products.map(metabolite => metabolite.id);

            if (reactantMetabolites.includes(metaboliteName) || productMetabolites.includes(metaboliteName)) {
                for (let gene of reaction.genes) {
                    if (gene.id !== null) {
                        let geneId = gene.id;
                        let geneInfo = geneSheet.find(geneData => geneData['GeneSymbol'] === geneId);
                        
                        if (geneInfo) {
                            let qValue = geneInfo['Qvalue'];
                            let zScore = norminv(1 - qValue); 
                            geneInfoDict[geneId] = zScore;
                        }
                    }
                }
            }
        }

        pathwayInfo.push({ Metabolite: metaboliteName, Gene_Info: geneInfoDict });
    }

    return pathwayInfo;
}



